<template>
  <div class="auth-container">
    <div class="auth-header">
      <div class="header-content">
        <img src="../assets/logo.png" alt="logo" class="logo" />
        <h1></h1>
      </div>
    </div>
    <div class="auth-box">
      <h2></h2>
      <form @submit.prevent="handleLogin" class="auth-form">
        <div class="form-group">
          <div class="input-wrapper phone-input-wrapper">
            <select v-model="countryCode" class="country-code">
              <option value="+86">+86</option>
            </select>
            <input
              type="tel"
              v-model="phone"
              placeholder=""
              class="phone-input"
              required
              pattern="[0-9]{11}"
            />
          </div>
        </div>
        
        <div class="form-group">
          <div class="input-wrapper password-input-wrapper">
            <input
              type="password"
              v-model="password"
              placeholder=""
              class="password-input"
              required
            />
          </div>
          <button type="button" class="forgot-password" @click="handleForgotPassword">?</button>
        </div>
        
        <div class="form-group">
          <div class="input-wrapper role-select-wrapper">
            <select v-model="userRole" class="role-select" required>
              <option value=""></option>
              <option value="user"></option>
              <option value="admin"></option>
            </select>
          </div>
        </div>
        
        <button type="submit" class="primary-button login-button" :disabled="isLoading">
          <span v-if="!isLoading"></span>
          <span v-else>...</span>
        </button>
        
        <div class="alternative-login">
          <button type="button" class="secondary-button verify-login" @click="switchToVerifyLogin"></button>
        </div>
      </form>
      
      <div class="quick-register">
        <span></span>
        <button type="button" class="link-button register-button" @click="handleRegister"></button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'LoginPage',
  data() {
    return {
      countryCode: '+86',
      phone: '',
      password: '',
      userRole: '',
      isLoading: false,
      isVerifyLogin: false
    }
  },
  methods: {
    async handleLogin() {
      // 
      if (!this.phone || !this.password || !this.userRole) {
        alert('');
        return;
      }
      
      if (this.phone.length !== 11) {
        alert('');
        return;
      }
      
      this.isLoading = true;
      
      try {
        // 
        await new Promise(resolve => setTimeout(resolve, 1500));
        
        // API
        console.log(':', {
          countryCode: this.countryCode,
          phone: this.phone,
          password: this.password,
          userRole: this.userRole
        });
        
        // 
        alert('');
        // 
        this.$emit('login-success', { username: this.phone, role: this.userRole });
      } catch (error) {
        console.error(':', error);
        alert('');
      } finally {
        this.isLoading = false;
      }
    },
    
    handleForgotPassword() {
      // 
      this.$emit('goto-reset');
    },
    
    
    handleRegister() {
      // 
      this.$emit('goto-register');
    }
  }
}
</script>

<style scoped>
/*  */
@import '../assets/styles.css';

/*  */
/*  -  */
.auth-form .form-group .input-wrapper.phone-input-wrapper .country-code {
  padding: 0 8px !important;
  min-width: 100px !important;
  width: 100px !important;
  max-width: 100px !important;
  font-size: 14px !important;
  height: 36px !important;
  background-color: #ffffff !important;
  border: none !important;
  border-radius: 0 !important;
  margin: 0 !important;
  flex-shrink: 0;
}

/*  */
.auth-form .form-group .input-wrapper.phone-input-wrapper .phone-input {
  padding: 0 12px !important;
  height: 36px !important;
  border: none !important;
  border-radius: 0 !important;
  flex: 1 !important;
}

/*  */
.auth-form .form-group .input-wrapper.phone-input-wrapper {
  height: 36px !important;
  display: flex !important;
  align-items: center !important;
  border: 1px solid #d9d9d9 !important;
  border-radius: 4px !important;
  overflow: hidden !important;
}

/*  */
.auth-form .form-group .input-wrapper.role-select-wrapper {
  height: 36px !important;
  display: flex !important;
  align-items: center !important;
  border: 1px solid #d9d9d9 !important;
  border-radius: 4px !important;
  overflow: hidden !important;
}

/*  */
.auth-form .form-group .input-wrapper.role-select-wrapper .role-select {
  width: 100% !important;
  padding: 0 12px !important;
  height: 36px !important;
  font-size: 14px !important;
  background-color: #ffffff !important;
  border: none !important;
  border-radius: 0 !important;
  outline: none !important;
}
</style>