<template>
  <main-layout page-title="">
    <div class="admin-requests-content">
      <!--  -->
      <div class="search-filter">
        <div class="search-input-wrapper">
          <input type="text" v-model="searchKeyword" class="search-input" placeholder="">
          <span class="search-icon"></span>
        </div>
      </div>
      
      <!--  -->
      <div class="requests-table-container">
        <table class="requests-table">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th>GPU</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="request in filteredRequests" :key="request.id">
              <td class="applicant">{{ request.applicant }}</td>
              <td class="score">
                <div class="score-container">
                  <span class="score-value">{{ request.score || 0 }}</span>
                  <div class="score-stars">
                    <span v-for="n in 5" :key="n" :class="['star', { 'active': n <= Math.floor(request.score / 2) }]">
                      
                    </span>
                  </div>
                </div>
              </td>
              <td class="resource-info">
                <div class="resource-type">{{ getResourceTypeText(request.resourceType) }}</div>
                <div class="request-title">{{ request.title }}</div>
                <div class="request-status">
                  <span class="status-badge" :class="request.status">
                    {{ getStatusText(request.status) }}
                  </span>
                </div>
              </td>
              <td class="gpu-info">
                <span v-if="request.gpuInfo" class="gpu-details">
                  {{ request.gpuInfo.range }} ({{ request.gpuInfo.count }})
                </span>
                <span v-else-if="request.status === 'approved'" class="no-gpu-assigned"></span>
                <span v-else class="no-gpu"></span>
              </td>
              <td class="actions">
                <button class="action-btn edit-btn" @click="editRequest(request.id)" title="">
                  
                </button>
                <button class="action-btn view-btn" @click="viewRequestDetails(request.id)" title="">
                  
                </button>
                <button class="action-btn delete-btn" @click="deleteRequest(request.id)" title="">
                  
                </button>
                <button v-if="request.status === 'pending'" class="action-btn allocate-btn" @click="allocateServer(request.id)" title="GPU">
                  
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      
      <!--  -->
      <div class="pagination">
        <div class="page-info">
           {{ (currentPage - 1) * pageSize + 1 }} - {{ Math.min(currentPage * pageSize, filteredRequests.length) }}  {{ filteredRequests.length }} 
        </div>
        <div class="page-controls">
          <button class="page-btn" :disabled="currentPage === 1" @click="changePage(currentPage - 1)"></button>
          <span class="page-num">{{ currentPage }} / {{ Math.ceil(filteredRequests.length / pageSize) }}</span>
          <button class="page-btn" :disabled="currentPage === Math.ceil(filteredRequests.length / pageSize)" @click="changePage(currentPage + 1)"></button>
        </div>
      </div>
      
      <!-- GPU -->
      <div v-if="showAllocateDialog" class="dialog-overlay" @click="closeAllocateDialog">
        <div class="dialog-content" @click.stop>
          <div class="dialog-header">
            <h3>GPU</h3>
            <button class="close-btn" @click="closeAllocateDialog"></button>
          </div>
          <div class="dialog-body">
            <div class="form-group">
              <label>:</label>
              <select v-model="selectedServerId" class="form-select">
                <option value=""></option>
                <option v-for="server in availableServers" :key="server.id" :value="server.id">
                  {{ server.name }} ({{ server.ip }}) - : {{ server.availableGpus.length }}
                </option>
              </select>
            </div>
            <div class="form-group" v-if="selectedServer">
              <label>:</label>
              <div class="gpu-selection">
                <select v-model="selectedGpuStart" class="form-select gpu-select">
                  <option value=""></option>
                  <option v-for="gpu in selectedServer.availableGpus" :key="gpu" :value="gpu">
                     #{{ gpu }}
                  </option>
                </select>
                <span class="gpu-separator"></span>
                <select v-model="selectedGpuCount" class="form-select gpu-select">
                  <option value="1">1</option>
                  <option value="2">2</option>
                  <option value="3">3</option>
                  <option value="4">4</option>
                </select>
              </div>
            </div>
          </div>
          <div class="dialog-footer">
            <button class="cancel-btn" @click="closeAllocateDialog"></button>
            <button class="confirm-btn" @click="confirmAllocate" :disabled="!canAllocate"></button>
          </div>
        </div>
      </div>
    </div>
  </main-layout>
</template>

<script>
import MainLayout from './MainLayout.vue'

export default {
  name: 'AdminRequests',
  components: {
    MainLayout
  },
  props: {
    userRole: {
      type: String,
      default: 'admin'
    }
  },
  data() {
    return {
      // 
      searchKeyword: '',
      currentPage: 1,
      pageSize: 10,
      
      // GPU
      showAllocateDialog: false,
      selectedRequestId: null,
      selectedServerId: '',
      selectedGpuStart: '',
      selectedGpuCount: '1',
      
      //  - 
      requestsList: [
        {
          id: 'REQ20231120001',
          title: '',
          resourceType: 'gpu',
          status: 'approved',
          applyTime: '2023-11-20 09:30',
          applicant: '',
          score: 9.5,
          allocatedServer: {
            name: 'GPU1',
            ip: '192.168.1.101'
          },
          gpuInfo: {
            range: '0-3',
            count: 4
          }
        },
        {
          id: 'REQ20231115002',
          title: '',
          resourceType: 'gpu',
          status: 'processing',
          applyTime: '2023-11-15 14:20',
          applicant: '',
          score: 8.5,
          allocatedServer: {
            name: 'GPU2',
            ip: '192.168.1.102'
          },
          gpuInfo: {
            range: '0-1',
            count: 2
          }
        },
        {
          id: 'REQ20231110003',
          title: '',
          resourceType: 'gpu',
          status: 'pending',
          applyTime: '2023-11-10 11:45',
          applicant: '',
          score: 9.0
        },
        {
          id: 'REQ20231105004',
          title: 'AI',
          resourceType: 'gpu',
          status: 'rejected',
          applyTime: '2023-11-05 16:10',
          applicant: '',
          score: 7.5
        },
        {
          id: 'REQ20231101005',
          title: '',
          resourceType: 'gpu',
          status: 'approved',
          applyTime: '2023-11-01 10:00',
          applicant: '',
          score: 8.8,
          allocatedServer: {
            name: 'GPU3',
            ip: '192.168.1.103'
          },
          gpuInfo: {
            range: '2-5',
            count: 4
          }
        },
        {
          id: 'REQ20231101006',
          title: '',
          resourceType: 'gpu',
          status: 'pending',
          applyTime: '2023-11-01 15:30',
          applicant: '',
          score: 9.2
        },
        {
          id: 'REQ20231101007',
          title: '',
          resourceType: 'gpu',
          status: 'processing',
          applyTime: '2023-11-02 09:15',
          applicant: '',
          score: 8.7,
          allocatedServer: {
            name: 'GPU4',
            ip: '192.168.1.104'
          },
          gpuInfo: {
            range: '0-5',
            count: 6
          }
        },
        {
          id: 'REQ20231101008',
          title: '',
          resourceType: 'gpu',
          status: 'approved',
          applyTime: '2023-11-03 11:20',
          applicant: '',
          score: 9.3,
          allocatedServer: {
            name: 'GPU5',
            ip: '192.168.1.105'
          },
          gpuInfo: {
            range: '4-7',
            count: 4
          }
        }
      ],
      
      // 
      availableServers: [
        {
          id: 1,
          name: 'GPU1',
          ip: '192.168.1.101',
          gpuCount: 8,
          availableGpus: [4, 5, 6, 7],
          cpuUsage: 20,
          memoryUsage: 25
        },
        {
          id: 2,
          name: 'GPU2',
          ip: '192.168.1.102',
          gpuCount: 8,
          availableGpus: [2, 3, 4, 5, 6, 7],
          cpuUsage: 25,
          memoryUsage: 30
        },
        {
          id: 3,
          name: 'GPU3',
          ip: '192.168.1.103',
          gpuCount: 8,
          availableGpus: [0, 1, 6, 7],
          cpuUsage: 30,
          memoryUsage: 20
        },
        {
          id: 4,
          name: 'GPU4',
          ip: '192.168.1.104',
          gpuCount: 8,
          availableGpus: [6, 7],
          cpuUsage: 15,
          memoryUsage: 18
        },
        {
          id: 5,
          name: 'GPU5',
          ip: '192.168.1.105',
          gpuCount: 8,
          availableGpus: [0, 1, 2, 3],
          cpuUsage: 35,
          memoryUsage: 32
        }
      ]
    }
  },
  computed: {
    // 
    filteredRequests() {
      let filtered = this.requestsList
      
      // 
      if (this.searchKeyword) {
        const keyword = this.searchKeyword.toLowerCase()
        filtered = filtered.filter(request => 
          request.applicant.toLowerCase().includes(keyword) ||
          request.title.toLowerCase().includes(keyword)
        )
      }
      
      return filtered
    },
    
    // 
    selectedServer() {
      return this.availableServers.find(server => server.id === parseInt(this.selectedServerId))
    },
    
    // 
    canAllocate() {
      return this.selectedServerId && this.selectedGpuStart && this.selectedGpuCount
    }
  },
  methods: {
    // 
    getStatusText(status) {
      const statusMap = {
        'pending': '',
        'approved': '',
        'rejected': '',
        'processing': '',
        'completed': ''
      }
      return statusMap[status] || status
    },
    
    // 
    getResourceTypeText(type) {
      const typeMap = {
        'server': '',
        'gpu': 'GPU',
        'storage': '',
        'software': '',
        'network': ''
      }
      return typeMap[type] || type
    },
    
    // 
    changePage(page) {
      this.currentPage = page
    },
    
    // GPU
    allocateServer(requestId) {
      this.selectedRequestId = requestId
      this.showAllocateDialog = true
    },
    
    // 
    closeAllocateDialog() {
      this.showAllocateDialog = false
      this.selectedRequestId = null
      this.selectedServerId = ''
      this.selectedGpuStart = ''
      this.selectedGpuCount = '1'
    },
    
    // 
    confirmAllocate() {
      if (!this.canAllocate) return
      
      const request = this.requestsList.find(r => r.id === this.selectedRequestId)
      const server = this.selectedServer
      
      if (request && server) {
        const gpuStart = parseInt(this.selectedGpuStart)
        const gpuCount = parseInt(this.selectedGpuCount)
        const gpuEnd = gpuStart + gpuCount - 1
        
        // 
        request.allocatedServer = {
          name: server.name,
          ip: server.ip
        }
        request.gpuInfo = {
          range: `${gpuStart}-${gpuEnd}`,
          count: gpuCount
        }
        request.status = 'approved'
        
        // 
        for (let i = gpuStart; i <= gpuEnd; i++) {
          const index = server.availableGpus.indexOf(i)
          if (index > -1) {
            server.availableGpus.splice(index, 1)
          }
        }
        
        alert(`GPU\n: ${request.applicant}\n: ${server.name}\n: ${gpuStart}-${gpuEnd}`)
        this.closeAllocateDialog()
      }
    },
    
    // 
    viewRequestDetails(requestId) {
      const request = this.requestsList.find(r => r.id === requestId)
      if (request) {
        alert(`\n\n: ${request.applicant}\n: ${request.title}\n: ${this.getResourceTypeText(request.resourceType)}\n: ${this.getStatusText(request.status)}\n: ${request.score}\n: ${request.applyTime}${request.gpuInfo ? `\n: ${request.gpuInfo.range} (${request.gpuInfo.count})` : ''}`)
      }
    },
    
    // 
    editRequest(requestId) {
      const request = this.requestsList.find(r => r.id === requestId)
      if (request) {
        alert(`\n\n: ${request.applicant}\n: ${request.title}\n: ${this.getStatusText(request.status)}`)
      }
    },
    
    // 
    deleteRequest(requestId) {
      if (confirm('')) {
        const index = this.requestsList.findIndex(r => r.id === requestId)
        if (index > -1) {
          // GPU
          const request = this.requestsList[index]
          if (request.allocatedServer && request.gpuInfo) {
            const server = this.availableServers.find(s => s.name === request.allocatedServer.name)
            if (server) {
              const [start, end] = request.gpuInfo.range.split('-').map(Number)
              for (let i = start; i <= end; i++) {
                if (!server.availableGpus.includes(i)) {
                  server.availableGpus.push(i)
                }
              }
              server.availableGpus.sort((a, b) => a - b)
            }
          }
          
          this.requestsList.splice(index, 1)
          alert('')
        }
      }
    }
  }
}
</script>

<style scoped>
/*  */
@import '../assets/styles.css';

.admin-requests-content {
  padding: 20px;
}

/*  */
.search-filter {
  display: flex;
  gap: 15px;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.search-input-wrapper {
  position: relative;
  flex: 1;
  min-width: 250px;
}

.search-input {
  width: 100%;
  padding: 10px 40px 10px 15px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 14px;
}

.search-icon {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  color: #666;
}

/*  */
.requests-table-container {
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  margin-bottom: 20px;
}

.requests-table {
  width: 100%;
  border-collapse: collapse;
}

.requests-table th {
  background: #f8f9fa;
  padding: 12px 15px;
  text-align: left;
  font-weight: 600;
  color: #333;
  border-bottom: 1px solid #eee;
  border-right: 2px solid #e6f7ff;
  white-space: nowrap;
}

.requests-table th:last-child {
  border-right: none;
}

.requests-table td {
  padding: 12px 15px;
  border-bottom: 1px solid #eee;
  border-right: 2px solid #e6f7ff;
  vertical-align: middle;
  background-color: #f0fdf4;
}

.requests-table td:last-child {
  border-right: none;
}

/*  */
.applicant {
  font-weight: 600;
  color: #333;
}

/*  */
.score-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 5px;
}

.score-value {
  font-size: 18px;
  font-weight: 700;
  color: #1890ff;
}

.score-stars {
  display: flex;
  gap: 2px;
}

.star {
  color: #d9d9d9;
  font-size: 14px;
  transition: all 0.3s;
}

.star.active {
  color: #faad14;
}

/*  */
.resource-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.resource-type {
  font-weight: 600;
  color: #1890ff;
  font-size: 14px;
}

.request-title {
  color: #333;
  font-size: 13px;
  max-width: 150px;
}

.request-status {
  margin-top: 4px;
}

/*  */
.status-badge {
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 500;
  text-align: center;
  display: inline-block;
  min-width: 60px;
}

.status-badge.pending {
  background: #fff7e6;
  color: #fa8c16;
}

.status-badge.approved {
  background: #f6ffed;
  color: #52c41a;
}

.status-badge.rejected {
  background: #fff2f0;
  color: #ff4d4f;
}

.status-badge.processing {
  background: #e6f7ff;
  color: #1890ff;
}

.status-badge.completed {
  background: #f9f0ff;
  color: #722ed1;
}

/* GPU */
.gpu-info {
  font-size: 13px;
}

.gpu-details {
  color: #52c41a;
  font-weight: 600;
}

.no-gpu-assigned {
  color: #fa8c16;
  font-weight: 500;
}

.no-gpu {
  color: #999;
}

/*  */
.actions {
  display: flex;
  gap: 5px;
  flex-wrap: wrap;
}

.action-btn {
  padding: 6px 10px;
  border: none;
  border-radius: 4px;
  font-size: 12px;
  cursor: pointer;
  transition: all 0.3s;
  min-width: 40px;
  text-align: center;
}

.edit-btn {
  background: #1890ff;
  color: white;
}

.edit-btn:hover {
  background: #40a9ff;
}

.view-btn {
  background: #52c41a;
  color: white;
}

.view-btn:hover {
  background: #73d13d;
}

.delete-btn {
  background: #ff4d4f;
  color: white;
}

.delete-btn:hover {
  background: #ff7875;
}

.allocate-btn {
  background: #faad14;
  color: white;
  font-size: 14px;
  padding: 6px 8px;
}

.allocate-btn:hover {
  background: #ffc53d;
}

/*  */
.pagination {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 0;
}

.page-info {
  color: #666;
  font-size: 14px;
}

.page-controls {
  display: flex;
  align-items: center;
  gap: 10px;
}

.page-btn {
  padding: 6px 12px;
  border: 1px solid #ddd;
  background: white;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s;
}

.page-btn:hover:not(:disabled) {
  background: #f0f0f0;
}

.page-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.page-num {
  color: #666;
  font-size: 14px;
}

/*  */
.dialog-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.dialog-content {
  background: white;
  border-radius: 8px;
  width: 90%;
  max-width: 500px;
  max-height: 80vh;
  overflow-y: auto;
}

.dialog-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #eee;
}

.dialog-header h3 {
  margin: 0;
  color: #333;
}

.close-btn {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #666;
  padding: 0;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.3s;
}

.close-btn:hover {
  background: #f0f0f0;
}

.dialog-body {
  padding: 20px;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: 500;
  color: #333;
}

.form-select {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 14px;
}

.gpu-selection {
  display: flex;
  align-items: center;
  gap: 10px;
}

.gpu-select {
  flex: 1;
}

.gpu-separator {
  color: #666;
  font-weight: 500;
}

.dialog-footer {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  padding: 20px;
  border-top: 1px solid #eee;
}

.cancel-btn, .confirm-btn {
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.3s;
}

.cancel-btn {
  background: #f0f0f0;
  color: #666;
}

.cancel-btn:hover {
  background: #d9d9d9;
}

.confirm-btn {
  background: #1890ff;
  color: white;
}

.confirm-btn:hover:not(:disabled) {
  background: #40a9ff;
}

.confirm-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/*  */
@media (max-width: 768px) {
  .search-filter {
    flex-direction: column;
  }
  
  .search-input-wrapper {
    width: 100%;
  }
  
  .requests-table-container {
    overflow-x: auto;
  }
  
  .requests-table {
    min-width: 800px;
  }
  
  .actions {
    flex-direction: column;
  }
  
  .dialog-content {
    width: 95%;
    margin: 20px;
  }
}
</style>