<template>
  <main-layout page-title="">
    <div class="resource-request-content">
      <!--  -->
      <div class="tabs">
        <button 
          class="tab-button" 
          v-if="userRole === 'user'"
          :class="{ active: activeTab === 'new-request' }"
          @click="switchTab('new-request')"
        >
          
        </button>
        <button 
          class="tab-button" 
          v-if="userRole === 'user'"
          :class="{ active: activeTab === 'my-requests' }"
          @click="switchTab('my-requests')"
        >
          
        </button>
        <button 
          class="tab-button" 
          v-if="userRole === 'admin'"
          :class="{ active: activeTab === 'all-requests' }"
          @click="switchTab('all-requests')"
        >
          
        </button>
      </div>
      
      <!--  -->
      <div v-if="activeTab === 'new-request'" class="request-form-container">
        <form @submit.prevent="handleSubmitRequest" class="request-form">
          <!--  -->
          <div class="form-section">
            <h3></h3>
            
            <div class="form-row">
              <div class="form-group">
                <label for="resourceType"> <span class="required">*</span></label>
                <select id="resourceType" v-model="formData.resourceType" class="form-select" required>
                  <option value=""></option>
                  <option value="server"></option>
                  <option value="server"></option>
                </select>
              </div>
              
              <div class="form-group">
                <label for="requestTitle"> <span class="required">*</span></label>
                <input type="text" id="requestTitle" v-model="formData.requestTitle" class="form-input" placeholder="" required>
              </div>
            </div>
            
            <div class="form-row">
              <div class="form-group">
                <label for="urgencyLevel"> <span class="required">*</span></label>
                <select id="urgencyLevel" v-model="formData.urgencyLevel" class="form-select" required>
                  <option value="normal"></option>
                  <option value="urgent"></option>
                  <option value="very-urgent"></option>
                </select>
              </div>
              
              <div class="form-group">
                <label for="expectedDate"> <span class="required">*</span></label>
                <input type="date" id="expectedDate" v-model="formData.expectedDate" class="form-input" required>
              </div>
            </div>
          </div>
          
          <!--  -->
          <div class="form-section">
            <h3></h3>
            
            <div class="form-row">
              <div class="form-group full-width">
                <label for="serverId">GPU <span class="required">*</span></label>
                <select id="serverId" v-model="formData.serverId" class="form-select" required @change="onServerChange">
                  <option value=""></option>
                  <option v-for="server in availableServers" :key="server.id" :value="server.id">
                    {{ server.name }} (IP: {{ server.ip }}, CPU: {{ server.cpuUsage }}%, : {{ server.memoryUsage }}%)
                  </option>
                </select>
              </div>
            </div>
            
            <div class="form-row">
              <div class="form-group">
                <label for="gpuIndex"> <span class="required">*</span></label>
                <select id="gpuIndex" v-model="formData.gpuIndex" class="form-select" required :disabled="!formData.serverId">
                  <option value=""></option>
                  <option v-for="gpu in availableGpus" :key="gpu" :value="gpu">
                     #{{ gpu }}
                  </option>
                </select>
              </div>
              
              <div class="form-group">
                <label for="gpuCount"> <span class="required">*</span></label>
                <input type="number" id="gpuCount" v-model="formData.gpuCount" class="form-input" min="1" :max="availableGpuCount" placeholder="1" required :disabled="!formData.serverId">
              </div>
            </div>

            
            <div class="form-row">
              <div class="form-group full-width">
                <label for="resourceDescription"> <span class="required">*</span></label>
                <textarea id="resourceDescription" v-model="formData.resourceDescription" class="form-textarea" rows="4" placeholder="GPU" required></textarea>
              </div>
            </div>
          </div>
          
          <!--  -->
          <div class="form-section">
            <h3></h3>
            
            <div class="form-row">
              <div class="form-group">
                <label for="projectContributionScore"> <span class="required">*</span></label>
                <input type="number" id="projectContributionScore" v-model="formData.projectContributionScore" class="form-input" min="1" max="10" placeholder="1-10" required>
              </div>
              
              <div class="form-group">
                <label for="researchAchievementScore"> <span class="required">*</span></label>
                <input type="number" id="researchAchievementScore" v-model="formData.researchAchievementScore" class="form-input" min="1" max="10" placeholder="1-10" required>
              </div>
            </div>
            
            <div class="form-row">
              <div class="form-group">
                <label for="resourceUsageEfficiencyScore"> <span class="required">*</span></label>
                <input type="number" id="resourceUsageEfficiencyScore" v-model="formData.resourceUsageEfficiencyScore" class="form-input" min="1" max="10" placeholder="1-10" required>
              </div>
              
              <div class="form-group">
                <label for="comprehensiveScore"></label>
                <input type="text" id="comprehensiveScore" :value="comprehensiveScore.toFixed(1)" class="form-input" disabled placeholder="">
              </div>
            </div>
          </div>
          
          <!--  -->
          <div class="form-section">
            <h3></h3>
            
            <div class="form-row">
              <div class="form-group">
                <label for="contactPerson"> <span class="required">*</span></label>
                <input type="text" id="contactPerson" v-model="formData.contactPerson" class="form-input" placeholder="" required>
              </div>
              
              <div class="form-group">
                <label for="contactPhone"> <span class="required">*</span></label>
                <input type="tel" id="contactPhone" v-model="formData.contactPhone" class="form-input" placeholder="" required>
              </div>
              
              <div class="form-group">
                <label for="contactEmail"> <span class="required">*</span></label>
                <input type="email" id="contactEmail" v-model="formData.contactEmail" class="form-input" placeholder="" required>
              </div>
            </div>
          </div>
          
          <!--  -->
          <div class="form-actions">
            <button type="button" class="cancel-button" @click="resetForm"></button>
            <button type="submit" class="submit-button" :disabled="isSubmitting">{{ isSubmitting ? '...' : '' }}</button>
          </div>
        </form>
      </div>
      
      <!--  -->
      <div v-else-if="activeTab === 'my-requests'" class="my-requests-container">
        <!--  -->
        <div class="search-filter">
          <div class="search-input-wrapper">
            <input type="text" v-model="searchKeyword" class="search-input" placeholder="ID">
            <span class="search-icon"></span>
          </div>
          
          <div class="filter-wrapper">
            <select v-model="statusFilter" class="filter-select">
              <option value=""></option>
              <option value="pending"></option>
              <option value="approved"></option>
              <option value="rejected"></option>
              <option value="processing"></option>
              <option value="completed"></option>
            </select>
          </div>
        </div>
        
        <!--  -->
        <div class="request-list">
          <div class="request-item" v-for="request in requestsList" :key="request.id">
            <div class="request-header">
              <div class="request-title">
                <h4>{{ request.title }}</h4>
                <span class="request-id">ID: {{ request.id }}</span>
              </div>
              <div class="request-status" :class="request.status">
                {{ getStatusText(request.status) }}
              </div>
            </div>
            
            <div class="request-content">
              <div class="request-info">
                <div class="info-item">
                  <span class="info-label">:</span>
                  <span class="info-value">{{ getResourceTypeText(request.resourceType) }}</span>
                </div>
                <div class="info-item">
                  <span class="info-label">:</span>
                  <span class="info-value">{{ request.applyTime }}</span>
                </div>
                <div class="info-item" v-if="request.allocatedServer">
                  <span class="info-label">:</span>
                  <span class="info-value allocated-server">{{ request.allocatedServer.name }} ({{ request.allocatedServer.ip }})</span>
                </div>
                <div class="info-item">
                  <span class="info-label">:</span>
                  <span class="info-value">{{ request.expectedDate }}</span>
                </div>
              </div>
              
              <div class="request-description">
                {{ request.description }}
              </div>
            </div>
            
            <div class="request-actions">
              <button class="view-button" @click="viewRequestDetail(request.id)"></button>
              <button v-if="request.status === 'pending'" class="cancel-button" @click="cancelRequest(request.id)"></button>
            </div>
          </div>
        </div>
        
        <!--  -->
        <div class="pagination">
          <div class="page-info">
             {{ (currentPage - 1) * pageSize + 1 }} - {{ Math.min(currentPage * pageSize, totalRequests) }}  {{ totalRequests }} 
          </div>
          <div class="page-controls">
            <button class="page-btn" :disabled="currentPage === 1" @click="changePage(currentPage - 1)"></button>
            <span class="page-num">{{ currentPage }} / {{ Math.ceil(totalRequests / pageSize) }}</span>
            <button class="page-btn" :disabled="currentPage === Math.ceil(totalRequests / pageSize)" @click="changePage(currentPage + 1)"></button>
          </div>
        </div>
      </div>
      
      <!--  -->
      <div v-else-if="activeTab === 'all-requests'" class="all-requests-container">
        <!--  -->
        <div class="search-filter">
          <div class="search-input-wrapper">
            <input type="text" v-model="adminSearchKeyword" class="search-input" placeholder="ID">
            <span class="search-icon"></span>
          </div>
          
          <div class="filter-wrapper">
            <select v-model="adminStatusFilter" class="filter-select">
              <option value=""></option>
              <option value="pending"></option>
              <option value="approved"></option>
              <option value="rejected"></option>
              <option value="processing"></option>
              <option value="completed"></option>
            </select>
          </div>
        </div>
        
        <!--  -->
        <div class="request-list">
          <div class="request-item" v-for="request in filteredAllRequests" :key="request.id">
            <div class="request-header">
              <div class="request-title">
                <h4>{{ request.title }}</h4>
                <span class="request-id">ID: {{ request.id }}</span>
                <span class="request-applicant">: {{ request.applicant }}</span>
                <span class="request-score" v-if="request.score">: {{ request.score }}</span>
              </div>
              <div class="request-status" :class="request.status">
                {{ getStatusText(request.status) }}
              </div>
            </div>
            
            <div class="request-content">
              <div class="request-info">
                <div class="info-item">
                  <span class="info-label">:</span>
                  <span class="info-value">{{ getResourceTypeText(request.resourceType) }}</span>
                </div>
                <div class="info-item">
                  <span class="info-label">:</span>
                  <span class="info-value">{{ request.applyTime }}</span>
                </div>
                <div class="info-item" v-if="request.allocatedServer">
                  <span class="info-label">:</span>
                  <span class="info-value allocated-server">{{ request.allocatedServer.name }} ({{ request.allocatedServer.ip }})</span>
                </div>
              </div>
              
              <div class="request-actions">
                <button 
                  class="primary-button" 
                  v-if="request.status === 'pending'"
                  @click="allocateServer(request.id)"
                >
                  
                </button>
                <button 
                  class="secondary-button" 
                  v-if="request.status === 'pending'"
                  @click="rejectRequest(request.id)"
                >
                  
                </button>
                <button 
                  class="secondary-button"
                  @click="viewRequestDetails(request.id)"
                >
                  
                </button>
              </div>
            </div>
          </div>
        </div>
        
        <!--  -->
        <div class="pagination">
          <div class="page-info">
             {{ (adminCurrentPage - 1) * adminPageSize + 1 }} - {{ Math.min(adminCurrentPage * adminPageSize, filteredAllRequests.length) }}  {{ filteredAllRequests.length }} 
          </div>
          <div class="page-controls">
            <button class="page-btn" :disabled="adminCurrentPage === 1" @click="changeAdminPage(adminCurrentPage - 1)"></button>
            <span class="page-num">{{ adminCurrentPage }} / {{ Math.ceil(filteredAllRequests.length / adminPageSize) }}</span>
            <button class="page-btn" :disabled="adminCurrentPage === Math.ceil(filteredAllRequests.length / adminPageSize)" @click="changeAdminPage(adminCurrentPage + 1)"></button>
          </div>
        </div>
      </div>
    </div>
  </main-layout>
</template>

<script>
import MainLayout from './MainLayout.vue'

export default {
  components: {
    MainLayout
  },
  name: 'ResourceRequest',
  props: {
    userRole: {
      type: String,
      default: 'user'
    }
  },
  data() {
    return {
      activeTab: 'new-request', // 'new-request'  'my-requests'
      
      // 
      formData: {
        resourceType: '',
        requestTitle: '',
        urgencyLevel: 'normal',
        expectedDate: '',
        cpuCount: '',
        memorySize: '',
        storageSize: '',
        // GPU
        serverId: '',
        gpuIndex: '',
        gpuCount: 1,
        resourceDescription: '',
        // 
        projectContributionScore: '',
        researchAchievementScore: '',
        resourceUsageEfficiencyScore: '',
        contactPerson: '',
        contactPhone: '',
        contactEmail: ''
      },
      
      isSubmitting: false,
      
      // 
      requestsList: [
        {
          id: 'REQ20231120001',
          title: '',
          resourceType: 'server',
          status: 'approved',
          applyTime: '2023-11-20 09:30',
          expectedDate: '2023-11-25',
          description: '8CPU16GB500GB'
        },
        {
          id: 'REQ20231115002',
          title: '',
          resourceType: 'storage',
          status: 'processing',
          applyTime: '2023-11-15 14:20',
          expectedDate: '2023-11-20',
          description: '1TB'
        },
        {
          id: 'REQ20231110003',
          title: '',
          resourceType: 'software',
          status: 'pending',
          applyTime: '2023-11-10 11:45',
          expectedDate: '2023-11-15',
          description: '3'
        },
        {
          id: 'REQ20231105004',
          title: '',
          resourceType: 'network',
          status: 'rejected',
          applyTime: '2023-11-05 16:10',
          expectedDate: '2023-11-10',
          description: '100Mbps500Mbps'
        },
        {
          id: 'REQ20231101005',
          title: 'GPU',
          resourceType: 'server',
          status: 'completed',
          applyTime: '2023-11-01 10:00',
          expectedDate: '2023-11-05',
          description: 'AIGPU4V100 GPU64GB'
        }
      ],
      
      searchKeyword: '',
      statusFilter: '',
      typeFilter: '',
      currentPage: 1,
      pageSize: 10,
      totalRequests: 5,
      
      // 
      allRequestsList: [
        {
          id: 'REQ20231120001',
          title: '',
          resourceType: 'server',
          status: 'approved',
          applyTime: '2023-11-20 09:30',
          expectedDate: '2023-11-25',
          description: '8CPU16GB500GB',
          applicant: '',
          score: 9.2,
          allocatedServer: {
            name: 'GPU1',
            ip: '192.168.1.101'
          }
        },
        {
          id: 'REQ20231115002',
          title: '',
          resourceType: 'storage',
          status: 'processing',
          applyTime: '2023-11-15 14:20',
          expectedDate: '2023-11-20',
          description: '1TB',
          applicant: '',
          score: 8.5
        },
        {
          id: 'REQ20231110003',
          title: '',
          resourceType: 'software',
          status: 'pending',
          applyTime: '2023-11-10 11:45',
          expectedDate: '2023-11-15',
          description: '3',
          applicant: '',
          score: 7.8
        },
        {
          id: 'REQ20231105004',
          title: '',
          resourceType: 'network',
          status: 'rejected',
          applyTime: '2023-11-05 16:10',
          expectedDate: '2023-11-10',
          description: '100Mbps500Mbps',
          applicant: '',
          score: 6.5
        },
        {
          id: 'REQ20231101005',
          title: 'GPU',
          resourceType: 'server',
          status: 'pending',
          applyTime: '2023-11-01 10:00',
          expectedDate: '2023-11-05',
          description: 'AIGPU4V100 GPU64GB',
          applicant: '',
          score: 9.5
        }
      ],
      
      //  - 78
      availableServers: [
        {
          id: 1,
          name: 'GPU1',
          ip: '192.168.1.101',
          gpuCount: 8,
          availableGpus: [0, 1, 2, 3, 4, 5, 6, 7],
          cpuUsage: 20,
          memoryUsage: 25
        },
        {
          id: 2,
          name: 'GPU2',
          ip: '192.168.1.102',
          gpuCount: 8,
          availableGpus: [0, 1, 2, 3, 4, 5, 6, 7],
          cpuUsage: 25,
          memoryUsage: 30
        },
        {
          id: 3,
          name: 'GPU3',
          ip: '192.168.1.103',
          gpuCount: 8,
          availableGpus: [0, 1, 2, 3, 4, 5, 6, 7],
          cpuUsage: 30,
          memoryUsage: 20
        },
        {
          id: 4,
          name: 'GPU4',
          ip: '192.168.1.104',
          gpuCount: 8,
          availableGpus: [0, 1, 2, 3, 4, 5, 6, 7],
          cpuUsage: 15,
          memoryUsage: 18
        },
        {
          id: 5,
          name: 'GPU5',
          ip: '192.168.1.105',
          gpuCount: 8,
          availableGpus: [0, 1, 2, 3, 4, 5, 6, 7],
          cpuUsage: 35,
          memoryUsage: 32
        },
        {
          id: 6,
          name: 'GPU6',
          ip: '192.168.1.106',
          gpuCount: 8,
          availableGpus: [0, 1, 2, 3, 4, 5, 6, 7],
          cpuUsage: 28,
          memoryUsage: 22
        },
        {
          id: 7,
          name: 'GPU7',
          ip: '192.168.1.107',
          gpuCount: 8,
          availableGpus: [0, 1, 2, 3, 4, 5, 6, 7],
          cpuUsage: 22,
          memoryUsage: 26
        }
      ],
      
      // 
      adminSearchKeyword: '',
      adminStatusFilter: '',
      adminCurrentPage: 1,
      adminPageSize: 10
    }
  },
  methods: {
    // 
    switchTab(tabName) {
      this.activeTab = tabName
    },
    
    // 
    async handleSubmitRequest() {
      try {
        // 
        if (!this.validateForm()) {
          return
        }
        
        this.isSubmitting = true
        // API
        await new Promise(resolve => setTimeout(resolve, 1500))
        
        // 
        const projectContributionScore = parseInt(this.formData.projectContributionScore)
        const researchAchievementScore = parseInt(this.formData.researchAchievementScore)
        const resourceUsageEfficiencyScore = parseInt(this.formData.resourceUsageEfficiencyScore)
        
        // (40%) + (30%) + (30%)
        const totalScore = projectContributionScore * 0.4 + researchAchievementScore * 0.3 + resourceUsageEfficiencyScore * 0.3
        
        // 
        const server = this.selectedServer
        
        // 
        alert(`\nIDREQ${Date.now().toString().slice(-8)}\n${totalScore.toFixed(1)}\nGPU\n- ${server.name} (${server.ip})\n- #${this.formData.gpuIndex}  #${parseInt(this.formData.gpuIndex) + parseInt(this.formData.gpuCount) - 1}\n- ${this.formData.gpuCount}`)
        this.resetForm()
      } catch (error) {
        console.error('', error)
        alert('')
      } finally {
        this.isSubmitting = false
      }
    },
    
    // 
      validateForm() {
        // 
        if (!this.formData.resourceType || !this.formData.requestTitle) {
          alert('')
          return false
        }
        
        // GPU
        if (!this.formData.serverId || !this.formData.gpuIndex || !this.formData.gpuCount) {
          alert('GPU')
          return false
        }
        
        // 1-10
        const scores = [
          this.formData.projectContributionScore,
          this.formData.researchAchievementScore,
          this.formData.resourceUsageEfficiencyScore
        ]
        
        for (const score of scores) {
          const numScore = parseInt(score)
          if (isNaN(numScore) || numScore < 1 || numScore > 10) {
            alert('1-10')
            return false
          }
        }
      
      // 
      if (!this.formData.contactPerson || !this.formData.contactPhone || !this.formData.contactEmail) {
        alert('')
        return false
      }
      
      return true
    },
    
    // 
      resetForm() {
        this.formData = {
          resourceType: '',
          requestTitle: '',
          urgencyLevel: 'normal',
          expectedDate: '',
          cpuCount: '',
          memorySize: '',
          storageSize: '',
          // GPU
          serverId: '',
          gpuIndex: '',
          gpuCount: 1,
          resourceDescription: '',
          // 
          projectContributionScore: '',
          researchAchievementScore: '',
          resourceUsageEfficiencyScore: '',
          contactPerson: '',
          contactPhone: '',
          contactEmail: ''
        }
      },
      
      // 
      onServerChange() {
        // 
        this.formData.gpuIndex = ''
        this.formData.gpuCount = 1
      },
    
    // 
    viewRequestDetail(requestId) {
      console.log('', requestId)
      // 
    },
    
    // 
    cancelRequest(requestId) {
      if (confirm('')) {
        console.log('', requestId)
        // API
        alert('')
      }
    },
    
    // 
    changePage(pageNum) {
      if (pageNum > 0 && pageNum <= Math.ceil(this.totalRequests / this.pageSize)) {
        this.currentPage = pageNum
      }
    },
    
    // 
    getStatusText(status) {
      const statusMap = {
        pending: '',
        approved: '',
        rejected: '',
        processing: '',
        completed: ''
      }
      return statusMap[status] || status
    },
    
    // 
    getResourceTypeText(type) {
      const typeMap = {
        server: '',
        storage: '',
        network: '',
        software: '',
        other: ''
      }
      return typeMap[type] || type
    },
    
    // 
    allocateServer(requestId) {
      // 
      const request = this.allRequestsList.find(item => item.id === requestId)
      if (!request) return
      
      // CPU
      const availableServer = this.availableServers
        .sort((a, b) => (a.cpuUsage + a.memoryUsage) - (b.cpuUsage + b.memoryUsage))
        .find(server => server.availableGpus.length > 0)
      
      if (availableServer) {
        // 
        request.allocatedServer = availableServer
        request.status = 'approved'
        alert(`: ${availableServer.name}`)
      } else {
        alert('')
      }
    },
    
    // 
    rejectRequest(requestId) {
      if (confirm('')) {
        const request = this.allRequestsList.find(item => item.id === requestId)
        if (request) {
          request.status = 'rejected'
          alert('')
        }
      }
    },
    
    // 
    viewRequestDetails(requestId) {
      console.log('', requestId)
      // 
    },
    
    // 
    changeAdminPage(pageNum) {
      if (pageNum > 0 && pageNum <= Math.ceil(this.filteredAllRequests.length / this.adminPageSize)) {
        this.adminCurrentPage = pageNum
      }
    }
  },
  computed: {
      // 
      selectedServer() {
        return this.availableServers.find(server => server.id === parseInt(this.formData.serverId))
      },
      
      // 
      comprehensiveScore() {
        const projectContributionScore = parseInt(this.formData.projectContributionScore)
        const researchAchievementScore = parseInt(this.formData.researchAchievementScore)
        const resourceUsageEfficiencyScore = parseInt(this.formData.resourceUsageEfficiencyScore)
        
        // 
        if (isNaN(projectContributionScore) || isNaN(researchAchievementScore) || isNaN(resourceUsageEfficiencyScore)) {
          return 0
        }
        
        // (40%) + (30%) + (30%)
        return projectContributionScore * 0.4 + researchAchievementScore * 0.3 + resourceUsageEfficiencyScore * 0.3
      },
      
      // 
    availableGpus() {
      if (!this.selectedServer) {
        return []
      }
      return this.selectedServer.availableGpus
    },
    
    // 
    availableGpuCount() {
      if (!this.selectedServer) {
        return 0
      }
      return this.selectedServer.availableGpus.length
    },
    
    // 
    filteredRequests() {
      return this.requestsList.filter(request => {
        const matchesSearch = !this.searchKeyword || 
          request.title.includes(this.searchKeyword) || 
          request.id.includes(this.searchKeyword)
        const matchesStatus = !this.statusFilter || request.status === this.statusFilter
        const matchesType = !this.typeFilter || request.resourceType === this.typeFilter
        return matchesSearch && matchesStatus && matchesType
      })
    },
    
    // ()
    filteredAllRequests() {
      return this.allRequestsList.filter(request => {
        const matchesSearch = !this.adminSearchKeyword || 
          request.title.includes(this.adminSearchKeyword) || 
          request.id.includes(this.adminSearchKeyword) || 
          request.applicant.includes(this.adminSearchKeyword)
        const matchesStatus = !this.adminStatusFilter || request.status === this.adminStatusFilter
        return matchesSearch && matchesStatus
      })
    }
  },
  watch: {
    // 
    searchKeyword() {
      this.currentPage = 1
    },
    statusFilter() {
      this.currentPage = 1
    },
    typeFilter() {
      this.currentPage = 1
    },
    
    // 
    adminSearchKeyword() {
      this.adminCurrentPage = 1
    },
    adminStatusFilter() {
      this.adminCurrentPage = 1
    }
  }
}
</script>

